<template>
    <v-container>
        <v-card>
            <v-container grid-list-md mb-0>
                <h2 class="text-md-center">Data Produksi Harian</h2>
                <v-layout row wrap style="margin:10px">
                    <v-flex xs6>
                         <v-btn depressed dark rounded style="text-transform: none !important;" color="green"  @click="dialog = true">
                            <v-icon size="18" class="mr-2">mdi-printer</v-icon>
                                Laporan Produksi
                        </v-btn>
                        <v-btn depressed dark rounded style="text-transform: none !important;" color="blue accent-3" class="ml-2"
                            @click="dialog3 = true">
                            <v-icon size="18" class="mr-2">mdi-printer</v-icon>
                            Laporan Pemakaian Bahan
                        </v-btn>
                    </v-flex>
                    <v-flex xs6 class="text-right">
                        <v-btn depressed dark rounded style="text-transform: none !important;" color="success" class="ml-2"
                            @click="dialog2 = true">
                            <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                            Posting Produksi
                        </v-btn>
                    </v-flex>
                </v-layout>
                <v-data-table :headers="headers" :items="harians" :search="keyword" :loading="load">
                    <template v-slot:body="{ items }">
                        <tbody>
                            <tr v-for="(item,index) in items" :key='item["id"]'>
                                <td class="title">{{ index + 1 }}</td>
                                <td class="title">{{ item["Tanggal"] }}</td>
                                <td class="title">{{ item["Kode Barang"] }}</td>
                                <td class="title">{{ item["JmlDs"] }}</td>
                                <td class="title">{{ item["JmlBotol"] }}</td>
                                <td v-if="item['Posted'] === 1"> <v-simple-checkbox :value="true" color="success" disabled></v-simple-checkbox></td>
                                <td v-else-if="item['Posted'] === 0"> <v-simple-checkbox disabled></v-simple-checkbox></td>
                                <td>
                                    <!-- <v-btn icon color="indigo" light @click="editHandler(item)">
                                        <v-icon>mdi-pencil</v-icon>
                                    </v-btn> -->
                                    <v-btn icon color="error" light @click='deleteData(item["id"])'>
                                        <v-icon>mdi-delete</v-icon>
                                    </v-btn>
                                </td>
                            </tr>
                        </tbody>
                    </template>
                </v-data-table>
            </v-container>
        </v-card>
        <v-dialog v-model="dialog2" persistent max-width="400px">
            <v-card>
                <v-card-title>
                    <span class="headline">Posting Produksi</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row no-gutters>
                            <v-col cols="4">
                                <h2 class="mt-2"> Tanggal</h2>
                            </v-col>
                            <v-col class="ml-4" cols="7">
                                <v-menu ref="menu2" v-model="menu2" :close-on-content-click="false"
                                    :return-value.sync="date" transition="scale-transition">
                                    <template v-slot:activator="{ on }">
                                        <v-text-field dense single-line outlined v-model="tanggalPost"
                                            label="Tanggal Produksi" class=" mr-4" readonly v-on="on"></v-text-field>
                                    </template>
                                    <v-date-picker v-model="tanggalPost" no-title scrollable>
                                        <v-spacer></v-spacer>
                                        <v-btn text color="primary" @click="menu2 = false">Cancel</v-btn>
                                        <v-btn text color="primary" @click="$refs.menu2.save(date)" >OK</v-btn>
                                    </v-date-picker>
                                </v-menu>
                            </v-col>
                        </v-row>
                    </v-container>
                    <!-- <small>*indicates required field</small> -->
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="red white--text" dense rounded @click="close">Close</v-btn>
                    <v-btn class="ml-4 white--text" dense color="blue darken-1" rounded @click="PostingProduksi()" :loading="loading">Save</v-btn>
                    <!-- <v-btn color="blue darken-1" text @click="close">Close</v-btn>
                    <v-btn color="blue darken-1" text @click="Posting()">Save</v-btn> -->
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog v-model="dialog" persistent max-width="400px">
            <v-card>
                <v-card-title>
                    <span class="headline">Cetak Laporan Produksi</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row no-gutters>
                            <v-col cols="4">
                                <h2 class="mt-2"> Tanggal</h2>
                            </v-col>
                            <v-col class="ml-4" cols="7">
                                <v-menu ref="menu" v-model="menu" :close-on-content-click="false"
                                    :return-value.sync="date" transition="scale-transition" min-width="290px">
                                    <template v-slot:activator="{ on }">
                                        <v-text-field dense single-line outlined v-model="form.tanggal"
                                            label="Tanggal" class="mr-4" readonly v-on="on"></v-text-field>
                                    </template>
                                    <v-date-picker v-model="form.tanggal" no-title scrollable>
                                        <v-spacer></v-spacer>
                                        <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
                                        <v-btn text color="primary" @click="$refs.menu.save(date)" >OK</v-btn>
                                    </v-date-picker>
                                </v-menu>
                            </v-col>
                        </v-row>
                    </v-container>
                    <!-- <small>*indicates required field</small> -->
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="red white--text" dense rounded @click="close">Close</v-btn>
                    <v-btn class="ml-4 white--text" dense color="blue darken-1" rounded   @click="CetakLaporanProduksi()">Save</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-dialog v-model="dialog3" persistent max-width="400px">
            <v-card>
                <v-card-title>
                    <span class="headline">Laporan Pemakaian Bahan</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row no-gutters>
                            <v-col cols="4">
                                <h2 class="mt-2"> Tanggal</h2>
                            </v-col>
                            <v-col class="ml-4" cols="7">
                                <v-menu ref="menu3" v-model="menu3" :close-on-content-click="false"
                                    :return-value.sync="date" transition="scale-transition" min-width="290px">
                                    <template v-slot:activator="{ on }">
                                        <v-text-field dense single-line outlined v-model="form.tanggal"
                                            label="Tanggal" class="mr-4" readonly v-on="on"></v-text-field>
                                    </template>
                                    <v-date-picker v-model="form.tanggal" no-title scrollable>
                                        <v-spacer></v-spacer>
                                        <v-btn text color="primary" @click="menu3 = false">Cancel</v-btn>
                                        <v-btn text color="primary" @click="$refs.menu3.save(date)" >OK</v-btn>
                                    </v-date-picker>
                                </v-menu>
                            </v-col>
                        </v-row>
                    </v-container>
                    <!-- <small>*indicates required field</small> -->
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="red white--text" dense rounded @click="close">Close</v-btn>
                    <v-btn class="ml-4 white--text" dense color="blue darken-1" rounded   @click="CetakLaporanPemakaianBahan()">Save</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-snackbar v-model="snackbar" :color="color" :multi-line="true" :timeout="3000">
            {{ text }}
            <v-btn dark text @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                tanggalPost: '',
                statusUpdate: true,
                date: new Date().toISOString().substr(0, 10),
                menu: false,
                menu2: false,
                menu3: false,
                dialog3: false,
                dialog2: false,
                dialog: false,
                keyword: '',
                headers: [{
                        text: 'No',
                        value: 'no',
                        class: 'title',
                    },
                    {
                        text: 'Tanggal',
                        value: 'Tanggal',
                        class: 'title',
                    },
                    {
                        text: 'Kode Barang',
                        value: 'Kode Barang',
                        class: 'title',
                    },
                    {
                        text: 'JmlDs',
                        value: 'JmlDs',
                        class: 'title',
                    },
                    {
                        text: 'JmlBotol',
                        value: 'JmlBotol',
                        class: 'title',
                    },
                    {
                        text: 'Posted',
                        value: 'Posted',
                        class: 'title',
                    },
                    {
                        text: 'Aksi',
                        value: '',
                        class: 'title',
                    },
                ],
                headers2: [
                    {
                        text: 'Kode Barang',
                        value: 'Kode Barang',
                        class: 'title',
                    },
                    {
                        text: 'No Bet',
                        value: 'No Bet',
                        class: 'title',
                    },
                    {
                        text: 'Jumlah',
                        value: 'Jumlah',
                        class: 'title',
                    },
                ],
                stoks: [],
                induk:[],
                harians: [],
                tempetiket: [],
                Postingoplosans: [],
                tampung: [],
                sisa: [],
                snackbar: false,
                color: null,
                text: '',
                load: false,
                form: {
                    tanggal: '',
                    barcode: '',
                },
                produksi: new FormData,
                mutstok: new FormData,
                typeInput: 'new',
                errors: '',
                updatedId: '',
                totalKeluar: 0,
                tanggalPerkiraan: '',
                batasanbet: '',
                loading: false,
            }
        },
        methods: {
            async PostingProduksi(){
                this.loading = true
                var uri = this.$apiUrl + 'produksis/dataharian1/' + this.tanggalPost
                var res1 = await this.$http.get(uri)
                this.induk = res1.data
                console.log(this.induk)
                for (var i in this.induk) {
                    this.harian = new FormData
                    this.harian.append('tanggal', this.induk[i]["Tanggal"])
                    this.harian.append('kodeBarang', this.induk[i]["Kode Barang"])
                    this.harian.append('noSuplier', '')
                    this.harian.append('noBukti', '')
                    this.harian.append('keterangan', 'Produksi')
                    this.harian.append('masuk', this.induk[i]['JumlahJadi'])
                    this.harian.append('keluar', 0)
                    var uri = this.$apiUrl + 'pemakaians'
                    await this.$http.post(uri, this.harian)
                    if (this.induk[i]["Tutup"] != 'KOSONG') {
                        console.log("masuk paling atas")
                        this.harian = new FormData
                        this.harian.append('tanggal', this.induk[i]["Tanggal"])
                        this.harian.append('kodeBarang', this.induk[i]["Tutup"])
                        this.harian.append('noSuplier', '')
                        this.harian.append('noBukti', '')
                        this.harian.append('keterangan', 'Produksi')
                        this.harian.append('masuk', 0)
                        this.harian.append('keluar', this.induk[i]['Jumlah'])
                        var uri = this.$apiUrl + 'pemakaians'
                        await this.$http.post(uri, this.harian)
                        this.mutstok = new FormData

                    }
                    if (this.induk[i]["Etiket Depan"] != 'KOSONG') {
                        this.harian = new FormData
                        this.harian.append('tanggal', this.induk[i]["Tanggal"])
                        this.harian.append('kodeBarang', this.induk[i]["Etiket Depan"])
                        this.harian.append('noSuplier', '')
                        this.harian.append('noBukti', '')
                        this.harian.append('keterangan', 'Produksi')
                        this.harian.append('masuk', 0)
                        this.harian.append('keluar', this.induk[i]['Jumlah'])
                        var uri = this.$apiUrl + 'pemakaians'
                        await this.$http.post(uri, this.harian)
                        this.mutstok = new FormData

                    }
                    if (this.induk[i]["Etiket Belakang"] != 'KOSONG') {
                        this.harian = new FormData
                        this.harian.append('tanggal', this.induk[i]["Tanggal"])
                        this.harian.append('kodeBarang', this.induk[i]["Etiket Belakang"])
                        this.harian.append('noSuplier', '')
                        this.harian.append('noBukti', '')
                        this.harian.append('keterangan', 'Produksi')
                        this.harian.append('masuk', 0)
                        this.harian.append('keluar', this.induk[i]['Jumlah'])
                        var uri = this.$apiUrl + 'pemakaians'
                        await this.$http.post(uri, this.harian)
                        this.mutstok = new FormData

                    }
                    if (this.induk[i]["Etiket Tambahan"] != 'KOSONG') {
                        this.harian = new FormData
                        this.harian.append('tanggal', this.induk[i]["Tanggal"])
                        this.harian.append('kodeBarang', this.induk[i]["Etiket Tambahan"])
                        this.harian.append('noSuplier', '')
                        this.harian.append('noBukti', '')
                        this.harian.append('keterangan', 'Produksi')
                        this.harian.append('masuk', 0)
                        this.harian.append('keluar', this.induk[i]['Jumlah'])
                        var uri = this.$apiUrl + 'pemakaians'
                        await this.$http.post(uri, this.harian)
                        this.mutstok = new FormData

                    }
                    if (this.induk[i]["Botol"] != 'KOSONG') {
                        this.harian = new FormData
                        this.harian.append('tanggal', this.induk[i]["Tanggal"])
                        this.harian.append('kodeBarang', this.induk[i]["Botol"])
                        this.harian.append('noSuplier', '')
                        this.harian.append('noBukti', '')
                        this.harian.append('keterangan', 'Produksi')
                        this.harian.append('masuk', 0)
                        this.harian.append('keluar', this.induk[i]['Jumlah'])
                        var uri = this.$apiUrl + 'pemakaians'
                        await this.$http.post(uri, this.harian)
                        this.mutstok = new FormData

                    }
                    if (this.induk[i]["Dos"] != 'KOSONG') {
                        this.harian = new FormData
                        this.harian.append('tanggal', this.induk[i]["Tanggal"])
                        this.harian.append('kodeBarang', this.induk[i]["Dos"])
                        this.harian.append('noSuplier', '')
                        this.harian.append('noBukti', '')
                        this.harian.append('keterangan', 'Produksi')
                        this.harian.append('masuk', 0)
                        this.harian.append('keluar', this.induk[i]['JumlahDos'])
                        var uri = this.$apiUrl + 'pemakaians'
                        await this.$http.post(uri, this.harian)
                        this.mutstok = new FormData

                    }
                    if (this.induk[i]["Label"] != 'KOSONG') {
                        this.harian = new FormData
                        this.harian.append('tanggal', this.induk[i]["Tanggal"])
                        this.harian.append('kodeBarang', this.induk[i]["Label"])
                        this.harian.append('noSuplier', '')
                        this.harian.append('noBukti', '')
                        this.harian.append('keterangan', 'Produksi')
                        this.harian.append('masuk', 0)
                        this.harian.append('keluar', this.induk[i]['JumlahDos'])
                        var uri = this.$apiUrl + 'pemakaians'
                        await this.$http.post(uri, this.harian)
                        this.mutstok = new FormData

                    }
                    if (this.induk[i]["Isi"] != 'KOSONG') {
                        this.harian = new FormData
                        this.harian.append('tanggal', this.induk[i]["Tanggal"])
                        this.harian.append('kodeBarang', this.induk[i]["Isi"])
                        this.harian.append('noSuplier', '')
                        this.harian.append('noBukti', '')
                        this.harian.append('keterangan', 'Produksi')
                        this.harian.append('masuk', 0)
                        this.harian.append('keluar', this.induk[i]['JumlahIsi'])
                        var uri = this.$apiUrl + 'pemakaians'
                        await this.$http.post(uri, this.harian)
                        this.mutstok = new FormData

                    }
                }
                // Posting Produksi to mutstok
                var uri = this.$apiUrl + 'pemakaians/postingproduksi'
                await this.$http.post(uri)
                // Update posted harian
                var uri1 = this.$apiUrl + 'pemakaians/updateharian/' + this.tanggalPost
                await this.$http.post(uri1)

                this.loading = false
                this.close();
                this.getData();
                
            },

            CetakLaporanPemakaianBahan(){
                this.$router.push({ path: `/laporanpemakaianbahan/${this.form.tanggal}` })
                this.close()
            },

            CetakLaporanProduksi(){
                this.$router.push({ path: `/laporanproduksi/${this.form.tanggal}` })
                this.close()
            },

            async getTempEtiket(){
                console.log("masuk sini oyyy")
                var uri = this.$apiUrl + 'produksis/reset'
                await this.$http.delete(uri)
                var uri1 = this.$apiUrl + 'produksis/tambahketmp/' + this.form.tanggal
                await this.$http.post(uri1)
                var uri2 = this.$apiUrl + 'produksis/gettempetiket'
                var x = await this.$http.get(uri2)
                this.tempetiket = x.data
            },




            async Posting() {
                this.loading = true
                var uri = this.$apiUrl + 'produksis/tanggal/' + this.tanggalPost
                await this.$http.post(uri)
                var uri1 = this.$apiUrl + 'produksis/updatebytanggal/' + this.tanggalPost
                await this.$http.post(uri1)
                this.snackbar = true; 
                this.color = 'green'; 
                this.text = 'Berhasil Posting'; 
                this.loading = false
                this.dialog2 = false
                this.resetForm()
                
            },
            getDataBarang() {
                var uri = this.$apiUrl + 'tblstoks/kode/13050'
                this.$http.get(uri).then(response => {
                    this.stoks = response.data.data
                })
            },
            getData() {
                var uri = this.$apiUrl + 'pemakaians/getdata'
                this.$http.get(uri).then(response => {
                    this.harians = response.data
                })
            },
            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
            
            
            editHandler(item) {
                this.typeInput = 'edit';
                this.statusUpdate = false
                this.dialog = true;
                this.form.tanggal = item["Tanggal"];
                this.form.kodeBarang = item["Kode Barang"];
                this.form.jumlahDosen = item["Jumlah Dosen"];
                this.form.jumlahBotol = item["Jumlah Botol"];
                this.updatedId = item["id"];
                console.log(this.updatedId)
            },

            deleteData(deleteId) { //mengahapus data
                var uri = this.$apiUrl + 'pemakaians/deleteharian/' + deleteId; //data dihapus berdasarkan id
                this.$http.delete(uri).then(response => {
                    this.snackbar = true;
                    this.text = 'Berhasil';
                    this.color = 'green'
                    this.deleteDialog = false;
                    this.getData();
                    this.Activity('Melakukan Delete Data Produksi Harian');
                }).catch(error => {
                    this.errors = error
                    this.snackbar = true;
                    this.text = 'Try Again';
                    this.color = 'red';
                })
            },
            setForm() {
                if (this.typeInput === 'new') {
                    this.sendData()
                    
                } else {
                    this.updateData()
                }
            },
            close() {
                this.dialog = false
                this.dialog2 = false
                this.dialog3 = false
                this.resetForm()
            },
            resetForm() {
                this.form= {
                    tanggal: '',
                    kodeBarang: '',
                    jumlahOplosan: 0,
                },
                this.tanggalPost = ''
                this.statusUpdate = true
                this.tempetiket = []
                this.tampung = []
            }
        },
        mounted() {
            this.getData();
            this.getDataBarang();
        },
    }
</script>
<style scoped>
  .custom-loader {
    animation: loader 1s infinite;
    display: flex;
  }
  @-moz-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-o-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
</style>